.datapanel {
  width: 90vw;

  border: 1px solid #a0a096;
  border-radius: 5px;

  padding: 10px;
  margin-top: 10px;

  overflow: scroll;
}

.panel_title {
  font-size: larger;
}

.t_timetable {
  border: 1px solid #a0a096;
  border-radius: 5px;

  text-align: center;
}

.t_timetable > thead > tr > td {
  width: 200px;
}
.t_timetable > tbody > tr > td {
  width: 200px;
}

.date_view {
  font-size: x-large;
  font-weight: 600;
}

.article_thumb {
  font-size: large;
  padding-left: 20px;
}